"use client";

import { useState } from "react";
import { createClient } from "@supabase/supabase-js";
import { useRouter } from "next/navigation";

// Supabase client
const supabase = createClient(
  "https://yemwpbhuhysdevnroqoz.supabase.co",
  "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6InllbXdwYmh1aHlzZGV2bnJvcW96Iiwicm9sZSI6ImFub24iLCJpYXQiOjE3NTk2ODAxNTksImV4cCI6MjA3NTI1NjE1OX0.wsVuE5pUOi9O2uvqNTb7DZBTkKqYXi6fiNB9hSh2SQA"
);

export default function SignUpPage() {
  const router = useRouter();

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [passwordMessage, setPasswordMessage] = useState("");

  // Validate password match
  const validatePassword = (pass, confirm) => {
    if (!confirm) {
      setPasswordMessage("");
      return false;
    }

    if (pass === confirm) {
      setPasswordMessage("✅ Passwords match");
      return true;
    } else {
      setPasswordMessage("❌ Passwords do not match");
      return false;
    }
  };

  async function handleSignUp() {
    if (password !== confirmPassword) {
      alert("Passwords do not match!");
      return;
    }

    const { data, error } = await supabase.auth.signUp({
      email,
      password,
    });

    if (error) {
      alert("❌ Sign up failed: " + error.message);
    } else {
      alert("✅ Sign up successful! Check your email for confirmation.");
      router.push("/login");
    }
  }

  return (
    <div className="min-h-screen overflow-x-hidden flex justify-center items-center">
      <div
        className="flex p-12 flex-col items-center bg-white/10 rounded-2xl shadow-[0_8px_20px_rgba(0,0,0,0.8)] gap-3 backdrop-blur-xl"
        style={{ boxShadow: "inset 0 0 20px rgba(255, 255, 255, 0.6)" }}
      >
        <i className="fa-regular fa-circle-user border-2 border-white rounded-lg p-2 text-4xl text-white shadow-lg"></i>

        <h1
          className="text-white font-bold text-3xl uppercase shadow-xs"
          style={{ fontFamily: "Poppins, sans-serif" }}
        >
          Sign Up
        </h1>

        <input
          placeholder="email"
          className="border-b-2 placeholder-white border-white bg-transparent focus:outline-none text-green-900 font-bold text-md placeholder-black"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
        />

        <input
          type="password"
          placeholder="password"
          className="border-b-2 placeholder-white border-white bg-transparent focus:outline-none text-purple-500 font-bold text-md placeholder-black"
          value={password}
          onChange={(e) => {
            setPassword(e.target.value);
            validatePassword(e.target.value, confirmPassword);
          }}
        />

        <input
          type="password"
          placeholder="Confirm password"
          className="border-b-2 placeholder-white border-white bg-transparent focus:outline-none text-purple-500 font-bold text-md placeholder-black"
          value={confirmPassword}
          onChange={(e) => {
            setConfirmPassword(e.target.value);
            validatePassword(password, e.target.value);
          }}
        />

        <p
          className={`text-sm mb-2 ${
            passwordMessage.includes("❌") ? "text-red-500" : "text-green-500"
          }`}
        >
          {passwordMessage}
        </p>

        <button
          onClick={handleSignUp}
          className="border-2 relative group border-white rounded-lg p-3 font-bold text-white cursor-pointer hover:text-green-900 transition duration-700 uppercase shadow-lg mt-4"
          style={{
            fontFamily: "Poppins, sans-serif",
            boxShadow: "inset 0 0 20px rgba(255, 255, 255, 0.3)",
          }}
        >
          Sign Up
          <span className="absolute top-0 left-0 w-0 h-full bg-white transition-all duration-500 group-hover:w-full z-[-1]"></span>
        </button>

        <p className="text-center text-md text-white font-semibold">
          Already have an account?{" "}
          <button
            onClick={() => router.push("/login")}
            className="cursor-pointer text-purple-500 hover:underline"
          >
            Sign In
          </button>
        </p>
      </div>
    </div>
  );
}
